import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { PlaceFormComponent } from '../place-form/place-form.component';
import { ResidentsListComponent } from '../../residents/residents-list/residents-list.component';
import { PrimarioListComponent } from '../primario-list/primario-list.component';
import { PlaceDetailComponent } from './place-detail.component';
import { AdsListComponent } from '../../ads/ads-list/ads-list.component';

const routes: Routes = [
	{
		path: '',
		component: PlaceDetailComponent,
		children: [
			{
				path: '',
				component: PlaceFormComponent
			},
			{
				path: 'residentes',
				component: ResidentsListComponent,
				data: {
					status: ['A']
				}
			},
			{
				path: 'residentes/pendientes',
				component: ResidentsListComponent,
				data: {
					status: ['P']
				}
			},
			{
				path: 'residentes/inactivos',
				component: ResidentsListComponent,
				data: {
					status: ['I', 'R', 'E']
				}
			},
			{
				path: 'primario-secundario',
				component: PrimarioListComponent
			},
			{
				path: 'anuncios',
				component: AdsListComponent
			}
		]
	}
];

@NgModule({
	declarations: [],
	imports: [
		RouterModule.forChild(routes),
		CommonModule
	],
	exports: [
		RouterModule
	]
})
export class PlaceDetailRoutingModule { }
